*,
:after,
:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}



html, body {
  width: 100%;
  height:100%;
}

body {
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    overflow: hidden;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.saman_bank { transition: all .2s ease-in-out; 
  opacity: 0.8 !important;
  z-index: 0;
}

.saman_bank:hover{
  opacity: 1 !important;
  filter: drop-shadow(1px 1px 10px #10ffc379);
}

.saman_bank:active{
  filter: drop-shadow(1px 1px 15px #10ffc3);
}

.root_v{
  width: 100% !important;
  height: 100% !important;
}

.leaflet-popup-content-wrapper{
  background-color: #94949450;
  width: 20vw !important;

}

.leaflet-popup-tip-container{
  opacity: 0;
}


.marker-cluster { 
  z-index: 100;
  opacity: 0.8 !important;
  transition: all .2s ease-in-out; 
}

.marker-cluster:hover{
  opacity: 1 !important;
  filter: drop-shadow(1px 1px 10px #a8a8a8ab);
}

.marker-cluster:active{
  filter: drop-shadow(1px 1px 15px #f8f8f8);
}
